@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;500;600;700;800&display=swap);
* {
    box-sizing: border-box;
}
html {
    font-size: calc(16px + 1vw);
    scroll-behavior: smooth;
}
body {
    margin: 0;
    font-family: 'Public Sans', sans-serif;
    font-weight: 500;
    background-color: #252734;
}
ul {
    padding: 0;
    list-style: none;
}
a, li {
    text-decoration: none;
}
span {
    color: #1A9ED8;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 100;
    height: 60px;
    width: 100%;
    padding: 10px 12% 0 12%;
    transition: .3s ease;
    background-color: #333646;
}
.header.hide {
    top: -60px;
}
.logotipo {
    height: 100%;
    margin: 0;
}
.logotipo-img {
    height: 100%;
}
.navigation {
    width: 35%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
}
.navigation-item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.navigation-link {
    height: 50%;
    color:#dddddd;
    font-size: .55rem;
    transition: .3s;
}
.navigation-link::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background-color: #dddddd;
    transition: width .3s;
}
.navigation-link:hover {
    color: white;
}
.navigation-link:hover::after {
    width: 100%;
    transition: width .3s;
}

.hamburguer {
    display: none;
}


@media (max-width: 1040px) {
    .navigation {
        width: 45%;
    }
    .navigation-link {
        font-size: .6rem;
    }
}

@media (max-width: 876px) {
    .navigation {
        width: 55%;
    }
}

@media (max-width: 768px) {
    .header {
        padding: 10px 5% 0 5%;
    }
    .navigation {
        visibility: hidden;
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: absolute;
        z-index: 10;
        top: 0;
        left: 0;
        background-color: #333646;
        margin: 0;
        -webkit-clip-path: circle(100px at 110% -10%);
        clip-path: circle(100px at 110% -10%);
        transition: all 1.5s ease-in-out;
    }
    .navigation.visible {
        visibility: visible;
        -webkit-clip-path: circle(1200px at 110% -10%);
        clip-path: circle(1200px at 110% -10%);
    }
    .navigation-item {
        height: 40px;
    }
    .navigation-link {
        font-size: 1.1rem;
        height: 100%;
    }
    .navigation-link::after {
        height: 1px;
    }
    .hamburguer {
        width: 50px;
        height: 50px;
        padding: 8px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: relative;
        z-index: 20;
        box-sizing: border-box;
        cursor: pointer;
        background-color: #333646;
        border: none;
        outline: none;
    }
    .hamburguer__line {
        width: 100%;
        height: 2px;
        background-color: white;

    }
}

.left {
    position: fixed;
    top: 40%;
    left: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.left__social {
    height: 180px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.left__link {
    color: #bbbbbb;
    font-size: .8rem;
    transition: .3s ease;
}
.left__link:hover {
    color: white;
}

.left__line {
    border-left: 1px solid #bbbbbb;
    height: 300px;
}



.right {
    position: fixed;
    top: 40%;
    right: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.right__email {
    font-size: 13px;
    font-weight: 100;
    color: #bbbbbb;
    -webkit-writing-mode: vertical-lr;
        -ms-writing-mode: tb-lr;
            writing-mode: vertical-lr;
    height: 180px;
    margin: 25px 0;
    transition: .3s ease;
}
.right__email:hover {
    color: white;
}

.right__line {
    border-right: 1px solid #bbbbbb;
    height: 300px;
}


@media (max-width: 940px) {
    .left {
        display: none;
    }
    .right {
        display: none;
    }
}
.main {
    display: flex;
    flex-direction: column;
    padding: 45px 12%;
    margin-top: 60px;
}
.main > p {
    font-size: .6rem;
    margin: 15px 0;
    color: #bbbbbb;
    width: 50%;
    line-height: 1.2rem;
}
.main > h1 {
    margin: 5px 0;
    color: white;
    font-size: 2.2rem;
}
.main > .main__name {
    color: #1A9ED8;
}
.main__button {
    font-size: .6rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: 1px solid #1A9ED8;
    border-radius: 5px;
    background-color: transparent;
    color: #1A9ED8;
    padding: 15px 30px;
    margin-top: 25px;
    transition: .5s all;
}
.main__button:hover {
    background-color: #1A9ED8;
    color: white;
    transform: translateY(-5px);
}

@media (max-width: 1040px) {
    .main {
        padding-top: 0;
        padding-bottom: 0;
    }
    .main > p {
        font-size: .7rem;
        margin: 25px 0;
        width: 75%;
    }
}

@media (max-width: 768px) {
    .main {
        padding-left: 5%;
        padding-right: 5%;
        padding-bottom: 0;
    }
    .main > .main__based {
        width: 65%;
    }
}

@media (max-width: 600px) {
    .main {
        align-items: center;
        padding: 20px;
    }
    .main > * {
        text-align: center;
    }
    .main > .main__based {
        width: 90%;
    }
}


@media (max-width: 420px) {
    .main > p {
        margin: 15px 0; 
    }
}
.about {
    width: 100%;
    display: flex;
    padding: 80px 15%;
}
.about__text {
    width: 60%;
    margin: 0 15px;
}

.about__title {
    color: white;
    font-size: 1.2rem;
}

.about__text > p {
    color: #bbbbbb;
    font-size: .6rem;
    margin: 40px 0;
}

.about__skills {
    display: flex;
    align-items: center;
}
.about__list {
    margin: 0 80px 0 0;
}
.about__list > li > svg {
    transform: translate(-6px, 6px);
    color:  #1A9ED8;
}

.about__list > li {
    color: #bbbbbb;
    font-size: .45rem;
}
.about__list > li:hover {
    color: white;
}







.about__image {
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 15px;
}
.about__image > div {
    width: 100%;
    border-radius: 50%;
    overflow: hidden;
    
}


.about__image > div > img {
    width: 100%;
    background-color: #1A9ED8;
}



@media screen and (max-width: 1300px) {
    .about {
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .about__list {
        margin: 0 70px 0 0;
    }
}

@media screen and (max-width: 1150px) {
    .about__list {
        margin: 0 60px 0 0;
    }
}

@media screen and (max-width: 1040px) {
    .about {
        padding-left: 12%;
        padding-right: 12%;
    }
    .about__list {
        margin: 0 40px 0 0;
    }
}

@media screen and (max-width: 876px) {
    .about {
        padding-left: 8%;
        padding-right: 8%;
    }
    .about__list {
        margin: 0 20px 0 0;
    }
}

@media screen and (max-width: 768px) {
    .about {
        flex-direction: column;
        align-items: center;
        padding: 0 5%;
    }
    .about__text {
        width: 100%;
        margin-bottom: 50px;
    }
    .about__text > p {
        margin: 25px 0;
    }
    .about__image {
        width: 45%;
    }
    .about__list {
        margin: 0;
    }
    .about__list > li {
        font-size: 0.5rem;
    }
    .about__skills {
        justify-content: space-around;
    }
}

@media screen and (max-width: 600px) {
    .about__text > * {
        text-align: center;
    }
    .about__image {
        width: 55%;
    }
}

@media screen and (max-width: 420px) {
    .about__image {
        width: 75%;
    }
}
.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 25px 12%;
  color: #bbbbbb;
}

.projects__title {
  color: white;
  text-align: center;
  font-size: 1.2rem;
}

.project__image {
  width: 55%;
  position: relative;
  overflow: hidden;
  border-radius: 5px;
}

.project__image:hover .project__overlay--one {
  opacity: 0;
}

.project__image > img {
  width: 100%;
  height: 100%;
}

.project__overlay--one {
  width: 100%;
  height: 100%;
  background-color: #1a9fd8b4;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  opacity: 1;
  transition: 1s ease-out;
}

.project__overlay--one > a {
  width: 100%;
  height: 100%;
}

.project {
  width: 100%;
  height: 400px;
  display: flex;
  align-items: center;
  position: relative;
}

.project__description {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 0;
  z-index: 20;
}

.project__two,
.project__four {
  flex-direction: row-reverse;
}

.project__two > .project__description,
.project__four > .project__description {
  left: 0;
  align-items: flex-start;
}

.project__two > .project__description > p,
.project__four > .project__description > p {
  text-align: start;
}

.project__description > h4 {
  font-size: 0.9rem;
  color: #1A9ED8;
}

.project__description > p {
  font-size: 0.6rem;
  text-align: end;
  background-color: #333646;
  border-radius: 3px;
  padding: 20px;
}

.project__technologies {
  display: flex;
  justify-content: space-between;
  width: 70%;
}

.project__technologies > li {
  font-size: 0.5rem;
  color: white;
}

@media screen and (max-width: 1040px) {
  .project {
    height: 350px;
  }
  .project__technologies {
    width: 80%;
  }
}

@media screen and (max-width: 876px) {
  .project {
    height: 320px;
  }
  .project__description > h4 {
    margin: 25px 0;
  }
  .project__technologies {
    margin: 20px 0;
    width: 85%;
  }
}

@media screen and (max-width: 768px) {
  .project__description > h4 {
    margin: 20px 0;
  }
  .project__description > p {
    margin: 0;
  }
  .project__technologies {
    margin: 15px 0;
  }
  .project__technologies > li {
    font-size: 0.46rem;
  }
}

@media screen and (max-width: 640px) {
  .project {
    height: auto;
    flex-direction: column-reverse;
  }
  .project__image {
    width: 100%;
  }
  .project__description {
    width: 80%;
    position: static;
    position: initial;
    align-items: center;
    margin: 30px 0;
  }
  .project__description > p {
    text-align: center;
  }
  .project__description > h4 {
    margin: 10px 0;
    text-align: center;
  }
  .project__two > .project__description,
  .project__four > .project__description {
    left: 0;
    align-items: center;
  }
}

.contact {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 25px 10%;
    color: #bbbbbb;
}
.contact__head {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact__head > h3 {
    font-size: 1.2rem;
    color: white;
    margin: 10px 0;
}
.contact__head > p {
    font-size: .6rem;
    width: 75%;
}

.contact__body {
    width: 100%;
    display: flex;
    margin-top: 25px;
}

.contact__body > * {
    width: 50%;
    padding: 0 5%;
}

.contact__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.contact__phone,
.contact__email {
    height: 80px;
    width: 100%;
    display: flex;
    margin-bottom: 20px;
}
.contact__icon {
    width: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 50%;
}
.contact__icon > svg {
    color: #333646;
}
.contact__text {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin-left: 20px;
}
.contact__phone > .contact__text {
    margin-left: 50px;
}
.contact__text > * {
    margin: 0;
}
.contact__text > h6 {
    color: #1A9ED8;
}
.contact__text > p {
    font-size: .5rem;
}


.contact__form {
    display: flex;
    flex-direction: column;
    background-color: #333646;
    align-items: flex-start;
    padding: 20px;
    border-radius: 5px;
}
.contact__form > * {
    margin: 10px 0;
}
.contact__form > h5 {
    font-size: .8rem;
    color: #ffffff;
}

.form-group {
    width: 100%;
    position: relative;
}

.form-group > input[type=text],
.form-group > input[type=email],
.form-group > textarea {
    width: 100%;
    padding: 5px 3px;
    margin: 10px 0;
    background-color: #333646;
    color: #ccc;
    font-size: .6rem;
    font-family: 'Public Sans', sans-serif;
    border: none;
    border-bottom: 2px solid #252734;
    outline: none;
    resize: none;
}

.form-group > span {
    position: absolute;
    left: 0;
    font-size: .5rem;
    padding: 5px 3px;
    margin: 10px 0;
    pointer-events: none;
    transition: .5s;
    color: #ccc;
}

.form-group > input:focus ~ span,
.form-group > textarea:focus ~ span,
.form-group > input:valid ~ span,
.form-group > textarea:valid ~ span {
    color: #1A9ED8;
    transform: translateY(-20px);
    font-size: .4rem;
}

.form-group > input[type=submit] {
    font-size: .5rem;
    color: white;
    background-color: #1A9ED8;
    padding: 8px 20px;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    transition: .3s ease-in-out;
}

@media screen and (max-width:876px) {
    .contact__body > .contact__info {
        padding: 0 4%;
    }
}

@media screen and (max-width:768px) {
    .contact {
        padding: 50px 5%;
    }
    .contact__head > p {
        width: 100%;
    }
    .contact__body > .contact__info {
        padding: 0 3%;
    }
    .contact__phone, .contact__email {
        margin-bottom: 40px;
    }
}

@media screen and (max-width: 640px) {
    .contact__body {
        flex-direction: column;
        align-items: center;
    }
    .contact__body > * {
        width: 70%;
    }
    .contact__info  {
        align-items: center;
        padding: 0;
    }
    .contact__phone,
    .contact__email {
        justify-content: space-between;
    }
    .contact__phone > .contact__text {
        margin: 0px 25px 0px 0px;
    }
    .contact__email > .contact__text {
        margin: 0px;
    }
}


@media screen and (max-width: 420px) {
    .contact__body > * {
        width: 90%;
    }
}
